import React from 'react';
import '../../css/Nubes.css';
const Nubes = ({top,left,right,bottom,scale})=>{
    return(
<div id="cloud" style={{top:top
                ,left:left
                ,right:right
                ,bottom:bottom
                ,transform:`scale(${scale})`}}>
  <div id="bottom_c"></div>
  <div id="right_c"></div>
  <div id="left_c"></div>
</div>

    );
}
export default Nubes;