import React from 'react';
import Load from '../../svg-loaders/audio.svg';
const Loader = ()=>{
    return(
        <div className="modal" style={{display:"block",position:"absolute",width:"100%",height:"100%"}}>
            <div className="modal-background"></div>
                <div className="modal-content">
                    <p className="image is-4by3">
                        <img src={Load} alt="" style={{width:"10vh",margin:"auto"}}alt=""/>
                    </p>
                </div>
        </div>
    );
}
export default Loader;