import React,{useEffect,useState} from 'react';
import'./useMsAnimate.css';
/**
 * 
 * @returns estas son las funcciones de calculos de posiciones y algunas de sus configuraciones
 */
const handlealtura=()=>{
    let middlescreen = (window.screen.height)/toolspos.screenanimate
    let screenanimatepart=middlescreen*toolspos.part
    
    return window.scrollY+ middlescreen+screenanimatepart;
  }
const handlealturacomponent=(element)=>{
    try{
       let altura = document.querySelectorAll(element);
       let arr = [];
       altura.forEach((el)=>{  
            arr=[...arr,{pos:el.offsetTop,ref:el}];
       })
       return arr;
       
    }catch(e){
        return"no se recibiÃ³ la clase "+e
    }  
  }


export let toolspos = {
    screenanimate:2,
    part:0,
}

/**
 * 
 * @param  {...any} element esta es la funccion del efecto
 */
export const config = {
    time:"1s",
    type:"ease-in",
}
export const useFade = (...element)=>{
    useEffect(()=>{
       let arr = handlealturacomponent(element);
       const handleanimate = ()=>{
        for(const el in arr){
            arr[el].ref.style.transition=`all ${config.time} ${config.type}`;
            if(arr[el].pos<handlealtura()){
                try{
                    arr[el].ref.style.opacity="1";
                   
             }catch(e){
                 console.warn("a ocurrido un error "+e);
             }
            }else{
                arr[el].ref.style.opacity="0";
            }
        }
 
        
       }
       window.addEventListener("scroll",handleanimate);
     },[]);
}

export const useTraslate = (posini,...element)=>{
    useEffect(()=>{
       let arr = handlealturacomponent(element);
       const handleanimate = ()=>{
        for(const el in arr){
            arr[el].ref.style.transition=`all ${config.time} ${config.type}`;
            arr[el].ref.style.transform=`translate(${posini}px)`;
            if(arr[el].pos<handlealtura()){
                try{
                    arr[el].ref.style.transform="translate(0px)"
             }catch(e){
                 console.warn("a ocurrido un error "+e);
             }
            }else{
                //arr[el].ref.style.transform="translate(0px)"
            }
        }
 
        
       }
       window.addEventListener("scroll",handleanimate);
     },[]);
}
export const useRotate = (deg,...element)=>{
    useEffect(()=>{
       let arr = handlealturacomponent(element);
       const handleanimate = ()=>{
        for(const el in arr){
            arr[el].ref.style.transition=`all ${config.time} ${config.type}`;
            //arr[el].ref.style.transform=`rotate(0)`;
            if(arr[el].pos<handlealtura()){
                try{
                    arr[el].ref.style.transform=`rotate(${deg}deg)`;
             }catch(e){
                 console.warn("a ocurrido un error "+e);
             }
            }else{
                arr[el].ref.style.transform=`rotate(${0}deg)`
            }
        }
 
        
       }
       window.addEventListener("scroll",handleanimate);
     },[]);
}
export const useTR = (deg,posini,...element)=>{
    useEffect(()=>{
       let arr = handlealturacomponent(element);
       const handleanimate = ()=>{
        for(const el in arr){
            arr[el].ref.style.transition=`all ${config.time} ${config.type}`;
            arr[el].ref.style.transform=`translate(${posini}px)`;
            if(arr[el].pos<handlealtura()){
                try{
                    arr[el].ref.style.transform=`translate(0px)rotate(${deg}deg)`;
             }catch(e){
                 console.warn("a ocurrido un error "+e);
             }
            }else{
                arr[el].ref.style.transform=`translate(${posini}px)rotate(0deg)`;
            }
        }
 
        
       }
       window.addEventListener("scroll",handleanimate);
     },[]);
}
export const useAdvanceTR = (posini,rotx,roty,rotz,deg,...element)=>{
    useEffect(()=>{
       let arr = handlealturacomponent(element);
       const handleanimate = ()=>{
        for(const el in arr){
        try{
            arr[el].ref.style.transition=`all ${config.time} ${config.type}`;
            arr[el].ref.style.transform=`translate(${posini}px)rotate(0deg)`
            if(arr[el].pos<handlealtura()){
                
                   arr[el].ref.style.transform=`translate(0px)rotate3d(${rotx},${roty},${rotz},${deg}deg)`
                   //arr[el].ref.style.transform=`translate(0px)rotate(720deg)scale(1)`  
             
            }else{
                
            }
        }catch(e){
                console.error("a ocurrido un error "+e);
            }
        }
 
        
       }
       window.addEventListener("scroll",handleanimate);
     },[]);
}
export const useAdvanceTR2 = (posini,scalei,deg,...element)=>{
    useEffect(()=>{
       let arr = handlealturacomponent(element);
       const handleanimate = ()=>{
        for(const el in arr){
        try{
            arr[el].ref.style.transition=`all ${config.time} ${config.type}`;
            arr[el].ref.style.transform=`translate(${posini}px)scale(${scalei})rotate(0deg)`
            if(arr[el].pos<handlealtura()){
                
                   arr[el].ref.style.transform=`translate(0px)scale(1)rotate(${deg}deg)`
                   //arr[el].ref.style.transform=`translate(0px)rotate(720deg)scale(1)`  
             
            }else{
                
            }
        }catch(e){
                console.error("a ocurrido un error "+e);
            }
        }
 
        
       }
       window.addEventListener("scroll",handleanimate);
     },[]);
}

