import React,{useEffect,useState} from 'react';

const Grid5 = (props) =>{

    const[prop,setProp] = useState({backgrounds : [],images : []});
    const img = "https://bulma.io/images/placeholders/1280x960.png";
    useEffect(()=>{
        try{
            if(props.images!=null)if(props.images.length>5)throw new Error("Array Index of Exception");
            if(props.backgrounds!=null)if(props.backgrounds.length>4)throw new Error("Array Index of Exception");
            
            setProp({
                backgrounds : props.backgrounds||["red","red","red","red"],
                images : props.images||[img,img,img,img,img]
            })
        }catch(e){
            console.error(e)
        }

    },[])
    return(
        <>
        <div className="columns is-multiline" style={{textAlign:"center",maxHeight:"66vh"}}>
        <div class="column is-full">{props.children}</div>
        <div className="column" >
            <img src={prop.images[0]} alt=""/>
        </div>
        <div className="column">
                <div className="columns is-multiline">
                    <div className="column is-half" >
                        <p style={{background:prop.backgrounds[0]}} class="bd-notification is-info">
                           fytdt 
                        </p>
                    </div>
                    <div className="column is-half">
                        <p style={{background:prop.backgrounds[1]}} class="bd-notification is-info">
                            Second nested column
                        </p>
                    </div>
                    <div className="column is-half" >
                        <p style={{background:prop.backgrounds[2]}} class="bd-notification is-info">
                            Second nested column
                        </p>
                    </div>
                    <div className="column is-half" >
                        <p style={{background:prop.backgrounds[3]}} class="bd-notification is-info">
                            Second nested column
                        </p>
                    </div>
                </div>
        </div>
        </div>
        </>
    );
}
export default Grid5;