import React,{lazy,Suspense} from 'react';
import '../../css/inicio.css';
import Grid5 from './grid5';
import '../../hooks/useMsAnimator';
import {useFade,toolspos,config} from '../../hooks/useMsAnimator';

const Section = ()=>{
    toolspos.screenanimate=3;
    toolspos.part=2;
    config.time="2s"
    //useFade("");
    return(
        <>
            <section class="section">
            <Grid5 
                backgrounds={["green","yellow","blue","pink"]}
               >
            </Grid5>
            </section>

            
        </>
    );
}
export default Section;