import React from 'react';
import image from '../../medias/images/ilustracion10s.png';
import '../../css/title.css';
import Nubes from '../section/Nubes';
const Header = ()=>{
    return(
        <section className="hero" style={{background:"rgb(59, 5, 94)"}}>
            <br/>
            <br/>
            <br/>
            <div className="hero-body">
                <div className="columns is-desktop">
                    <div className="column">
                        <p className="title anm " style={{fontFamily: "'Press Start 2P', cursive",color:"white"}}>
                            Bienvenido a MS Academy 
                        </p>
                        <p className="title anm " style={{fontFamily: "",color:"white"}}>
                             Una plataforma donde aprendera desarrollo web fullstack de forma Gratuita,
                             sin tarjeta de credito, sin letras chiquitas
                        </p>
                
                        <br/>
                        <p className="subtitle">
                            <div className="control">
                                <button style={{zIndex:"5"}} class="button is-primary is-rounded">Registrate Gartis</button>
                            </div>
                        </p>
                    </div>
                    <div className="column" style={{textAlign:"center"}}>
                    <img className="" src={image} style={{width:"45%",transform:"scale(2)"}}/>
                    </div>
                </div>
                <Nubes top="33%" left="20%" scale=".2" />
                <Nubes top="33%" left="28%" scale=".2" top="30%" />
                <Nubes top="33%" left="35%" scale=".2" top="20%" />
                <Nubes top="33%" left="2%" scale=".6"/>
            </div>
            <br/>
            <br/>
        </section>
    );
}
export default Header;